.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &--active {
    .social-block__link {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;

  .body-theme-blue & {
    color: $c-blue;
  }
  
  .body-theme-light-blue & {
    color: $c-blue-light;
  }

  .body-theme-dark & {
    color: $c-dark;
  }

  .body-theme-purple & {
    color: $c-purple;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 0;
    height: 0;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
  }

  .page-loaded & {
    &::before {
      width: 100%;
      height: 100%;
      transition-duration: 400ms;
      transition-delay: 400ms;
      transition-timing-function: cubic-bezier(0, 1, 0.3, 1);
    }
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    .body-theme-blue & {
      color: $c-blue-light;
    }
    
    .body-theme-light-blue & {
      color: $c-blue;
    }

    .body-theme-dark & {
      color: $c-purple;
    }

    .body-theme-purple & {
      color: $c-dark;
    }

    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    .body-theme-blue & {
      color: $c-blue-light;
      &::before {
        border-color: $c-blue-light;
      }
    }
    
    .body-theme-light-blue & {
      color: $c-blue;
      &::before {
        border-color: $c-blue;
      }
    }

    .body-theme-dark & {
      color: $c-purple;
      &::before {
        border-color: $c-purple;
      }
    }

    .body-theme-purple & {
      color: $c-dark;
      &::before {
        border-color: $c-dark;
      }
    }

    outline: none;

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transform: translateY(20px);
  transition: 300ms;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    .body-theme-blue & {
      color: $c-blue-light;
    }

    .body-theme-light-blue & {
      color: $c-blue;
    }

    .body-theme-dark & {
      color: $c-purple;
    }

    .body-theme-purple & {
      color: $c-dark;
    }
  }

  &:focus {
    .body-theme-blue & {
      color: $c-blue-light;
      border-color: $c-blue-light;
    }

    .body-theme-light-blue & {
      color: $c-blue;
      border-color: $c-blue;
    }

    .body-theme-dark & {
      color: $c-purple;
      border-color: $c-purple;
    }

    .body-theme-purple & {
      color: $c-dark;
      border-color: $c-dark;
    }

    background-color: $c-light;
    outline: none;
  }

  &:active,
  &:active:focus {

    .body-theme-blue & {
      color: $c-blue-light;
    }

    .body-theme-light-blue & {
      color: $c-blue;
    }

    .body-theme-dark & {
      color: $c-purple;
    }

    .body-theme-purple & {
      color: $c-dark;
    }

    border-color: $c-light;
    background-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    transition-delay: 100ms;

    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    transition-delay: 200ms;

    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
